  :root {
    --primary-color: #e0c521;
  }

  .header {
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .container { 
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }


  .card_homepage {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 150px; 
    height: 260px;
    border: 2px solid var(--primary-color);
    padding: 0.5rem 0.3rem;
    margin: 20px 10px;
    position: relative;
    border-radius: 5px;
    }
  
  
  .image__homepage {
    width: 150px;
    height: 150px;
    margin-top: 5px;  
    margin-right: auto; 
    margin-bottom: auto; 
    margin-left: auto; 
    object-fit: cover; 
    }


  .link {
    display: flex;
    justify-content: center; /* Горизонтальное выравнивание по центру */
    align-items: center; /* Вертикальное выравнивание по центру */
    padding: 0.6rem 0.8rem;
    font-size: 1.2rem;
    text-align: center;
    border: 0;
    outline: none;
    border-radius: 10px;
    width: 135px;
    height: 45px;
    box-shadow: 1px -3px 52px -5px rgba(0, 0, 0, 0.77);
    cursor: pointer;
    background-color: #cab320;
  }
  
  .link:hover {
    background-color: #ad9a1c;
  }
  
  .menu__title {
    font: italic normal bold 42px cursive;
    color: #e0c521;
    
  }

  .image__order {
    width: 50px;   
    height: 90%;  
    border: 2px solid #a31818;
    object-fit: cover;
    
    
  }

  .container_order {
  width: 440px;
  height: 52px;
  border: 2px solid #737070;
  display: flex;
  margin: auto;
  transform: rotate(-0deg);
  justify-content: space-between;
  
  }

  .title__order {
    text-align: center;
  }

 

  .border_image_order {
    display: flex;
    justify-content: center; /* Горизонтальное выравнивание по центру */
    align-items: center; 
    width: 50px;   
    height: 95%; 
    
    
  }

  .border_title_order {
    display: flex;
    justify-content: center; /* Горизонтальное выравнивание по центру */
    align-items: center; 
    width: 130px;   
    height: 50px;
    
    
  }
.heading_games {
  height: 65px;
  display: flex;
  justify-content: space-between; 
  align-items: flex-start;
  
}

.price{
  margin-top: 40px;
  margin-right: 5px;
  
}

.link_game{
  display: flex;
    justify-content: center; /* Горизонтальное выравнивание по центру */
    align-items: center; /* Вертикальное выравнивание по центру */
    padding: 0.6rem 0.8rem;
    font-size: 1.2rem;
    text-align: center;
    border: 0;
    outline: none;
    border-radius: 10px;
    width: 135px;
    height: 35px;
    box-shadow: 1px -3px 52px -5px rgba(0, 0, 0, 0.77);
    cursor: pointer;
    background-color: #cab320;
    margin-top: 5px;
}