:root {
  --primary-color: #e0c521;
}

.card {
  display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 150px; 
    height: 260px;
    border: 2px solid var(--primary-color);
    padding: 0.5rem 0.3rem;
    margin: 20px 10px;
    position: relative;
    border-radius: 5px;
}

.card__title {
  margin-top:0px;
  font-weight: 350;
  text-align: center;
}

.card__title .card__price {
  font-weight: bold;
}



.image__container {
  width: 140px;
  height: 140px;
  margin-top: auto;  
  margin-right: auto; 
  margin-bottom: auto; 
  margin-left: auto; 
  object-fit: cover;
}

.btn-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 0px;
  
}

.card__price {
  text-align: center;
  display: inline-block;
}

.card__badge {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: rgb(227, 112, 30);
  border-radius: 50%;
  transform: translate(20px, -20px) scale(1);
  color: #fff;
  font-weight: bold;
  text-align: center;
  border: 2px solid rgb(227, 112, 30);
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  animation: scaleanimation 1s ease infinite;
  opacity: 0.8;
}

@keyframes scaleanimation {
  to {
    transform: translate(20px, -20px) scale(1.05);
    opacity: 1;
  }
}

.card__badge--hidden {
  display: none;
}
